* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.header {
  display: flex;
  align-items: center;
  gap: 20px;
  background-color: gray;
  padding: 20px 0 20px 40px;
  margin-bottom: 40px;
}

.header img {
  height: 60px;
  width: auto;
}

.main {
  border-radius: 5px;
  background-color: #eeeeee;
  width: 80%;
  margin: 0 auto;
  margin-top: 40px;
}

.header h1 {
  font-size: 40px;
}

.Personal-section,
.Education-section,
.Work-section {
  margin: 10px;
}

.Personal-section h2,
.Education-section h2,
.Work-section h2 {
  font-size: 30px;
  margin-bottom: 10px;
}

.Personal-section form,
.Education-section form,
.Work-section form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 10px;
}

.Personal-section input,
.Personal-section textarea,
.Education-section input,
.Work-section input {
  font-size: 15px;
  padding: 5px;
}

.Personal-section button {
  padding: 5px 20px;
  margin: 10px 0;
}

.btn-part {
  display: flex;
  gap: 10px;
}

.block {
  display: block;
}

.btn-submit,
.btn-preview {
  display: block;
  padding: 5px 20px;
  margin: 10px auto;
  width: 98%;
}

.btn-preview {
  margin-top: 20px;
}

.btn-submit {
  display: block;
  padding: 5px 20px;
  margin: 10px auto;
  width: 98%;
}

.btn-edu,
.btn-work {
  padding: 5px 20px;
  margin: 10px 0;
}

.btn-add {
  display: block;
  padding: 5px 20px;
  margin: 10px auto;
  width: 50%;
}

/* PREVIEW */

.preview {
  color: white;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  box-sizing: border-box;
  border-radius: 5px;
  background-color: #eeeeee;
  width: 800px;
  margin: 0 auto;
}

.name-box {
  background-color: #143e72;
  padding: 20px 15px 30px 20px;
}

.name-box h1 {
  font-size: 40px;
  font-weight: bold;
}

.name-box h2 {
  font-size: 30px;
  font-weight: 200;
}

.preview-main {
  color: black;
  display: flex;
  height: 680px;
}

.preview-personal {
  width: 30%;
  background-color: #dddddd;
  padding: 20px;
  position: relative;
}
.personal-information {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.preview-data {
  width: 70%;
  padding: 20px;
}

.separator {
  width: 80%;
  height: 1px;
  background-color: rgba(136, 133, 133, 0.596);
  margin: 5px 0;
}

.title {
  color: #143e72;
}

.experience-el {
  margin: 20px 0;
  display: grid;
  grid-template-columns: 40% 50%;
  align-items: center;
  justify-items: start;
}

.experience-el p:last-child {
  grid-column: 2 / 3;
}

.experience-el .title {
  color: black;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 5px;
}

.experience-el .sub {
  font-weight: 300;
}

.description {
  margin: 20px 0;
  width: 80%;
}

.preview-btn {
  position: absolute;
  display: block;
  padding: 5px 20px;
  margin: 10px auto;
  width: 200px;
  left: 100px;
}

.preview-btn-section {
  display: flex;
  gap: 50px;
  align-items: center;
  justify-content: center;
  padding: 5px 20px;
  margin: 10px 0;
}

.preview-btn-section button {
  padding: 5px 20px;
  width: 200px;
}
